<template>
	<small>Slider input</small>
	<!--
	SLIDER INPUT NOT YET READY:
		slider_attributes
		slider library
	<div class="input-range-slider">
		<input type="text" :id="field_id" :name="field_id" v-model="value" :slider_attributes="slider_attributes"/>
	</div>
	-->

	<div class="slider_scope">
		<div ref="slider"></div>
	</div>

</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';
	import * as noUiSlider from 'nouislider';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
			}
		},
		methods: {
			changed() {
				var value = this.$refs.slider.noUiSlider.get();
				State.save_user_input(this.field_id, value);
			},
		},
		computed: {
			slider_attributes() {
				// should be pure string inside html, not slider_attributes parameter of input tag
				var slider_attributes = this.field_data.wrapper_attrs.split( 'value' ).join( '' ); // they used to send 'value' in dataset name
				// have to change it to object, string is useless for noUiSlider
				return slider_attributes;
			},
		},
		mounted() {
			var input_value = Functions.value_from_storage_or_session(this.field_id, this.field_data, 0);

			noUiSlider.create(
				this.$refs.slider,
				{
					start: input_value,
					connect: true,
					tooltips: [true],
					step: 1,
					range: {
						'min': 0,
						'max': 100
					},
					// pips: {
					// 	mode: 'range',
					// 	density: 3,
					// },
					format: {
						// 'to' the formatted value. Receives a number.
						to: function (value) {
							return value;
						},
						// 'from' the formatted value. Receives a string, should return a number.
						from: function (value) {
							return value;
						}
					}
				}
			);

			this.$refs.slider.noUiSlider.on('change', function(){ this.changed() }.bind( this ));
		},
	}
</script>

<style scoped lang="scss">
	// disables display: flex; from parent div.input-area
	.slider_scope {
		flex-basis: 100%;
	}
	.slider_scope::v-deep() {
		@import '@/assets/libraries/nouislider_5.15.0/nouislider.scss'
	}
</style>
