import State from '@/store/State.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Get_static_docs
*/
class Get_static_docs
{
	/**
	 * static_documents
	*/
	static_documents()
	{
		let requestParams = {
			action_name: 'getdocuments',
			method: 'GET',
			url: State.endpoint_url() + 'online/getdocuments/' + State.get_selected_product().product_id + '/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * get_static_document
	*/
	get_static_document(document_id)
	{
		let requestParams = {
			action_name: 'getdocument',
			method: 'GET',
			url: State.endpoint_url() + 'online/getdocument/' + State.get_selected_product().product_id + '/' + document_id + '/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * get_session_document
	*/
	get_session_document(document_id)
	{
		let requestParams = {
			action_name: 'getsessiondocument',
			method: 'GET',
			url: State.endpoint_url() + 'online/getsessiondocument/' + document_id + '/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * upload_session_document
	*/
	upload_session_document(payload)
	{
		let requestParams = {
			action_name: 'createsessiondocument',
			method: 'POST',
			body: payload,
			content_type: null, // 'multipart/form-data' filled by browser
			url: State.endpoint_url() + 'online/createsessiondocument/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Get_static_docs;
