import State from '@/store/State.js';

/**
 * Request_service
*/
class Request_service
{
	params = {/*
		url: '', // required
		method: '', // required
		action_name: '', // required
		counter: 0, // never set  COUNTER REMOVED
		body: null, // optional
		timeout: 25, // optional
		loop_max: 4, // optional COUNTER REMOVED
		content_type: 'application/json', // optional
	*/};
	headers = {};

	/**
	 * prepare_params
	*/
	prepare_params( params ) // this current class iteration
	{
		// store for later
		this.params = params;

		// getDefaultParams can be removed now
		// increase counter, or set first
		/* COUNTER REMOVED
		if ( this.params.counter === undefined ) {
			this.params.counter = 1;
		} else {
			this.params.counter++;
		}*/

		// default timeout is 25 seconds, some uses 10
		if ( this.params.timeout === undefined ) {
			this.params.timeout = 25;
		}

		// default loop_max is 4, but every request can set its own
		/* COUNTER REMOVED
		if ( this.params.loop_max === undefined ) {
			this.params.loop_max = 4;
		}*/

		/* THIS WAS REMOVED AND 'Content-Type' IS FILLED STRAIGHT TO ALLOW IT TO BE EMPTY (FILLED BY BROWSER) WHEN null
		if ( this.params.content_type === undefined ) {
			this.params.content_type = 'application/x-www-form-urlencoded';
		}
		*/

		// request header options
		this.headers = {
			method: this.params.method,
			mode: 'cors',
			cache: 'no-cache',
			credentials: 'omit',
			headers: {
				'x-user-scope': State.give_api_scope(),
				'Authorization': State.give_bearer(),
				// 'Content-Type': this.params.content_type,
				'Accept': 'application/json',
			},
			// body: null,
		}
		if ( this.params.method === 'POST' && this.params.body /*undefined|null|string*/ ) {
			this.headers.body = this.params.body;
		}
		if ( this.params.content_type === undefined ) {
			this.headers.headers['Content-Type'] = 'application/x-www-form-urlencoded';
		} else if ( this.params.content_type !== null ) {
			this.headers.headers['Content-Type'] = this.params.content_type;
		} // else we dont fill null, it is filled by browser

		// allow chaining
		return this;
	}

	/**
	 * unauthorized
	*/
	async unauthorized()
	{
		var backend_type = State.get_backend_type();
		if (backend_type === 'php') {
			var auth = await window.osm_php_backend.auth_service();
			State.save_bearer(auth);
			return true;
		} else if (backend_type === 'lukas_local') {
			var url = 'http://wrapper.local/authorize_frontend.php?entpoint_type=' + State.endpoint_type();
			return fetch(url).then(
				(response) => {
					return response.json();
				}
			).then(
				(result) => {
					State.save_bearer(result);
					console.log('NEW BEARER:'); console.log(result);
					return true;
				}
			);
		} else if (backend_type === 'vue') {
			console.log('VUE does not have 401 auth service, fill access_token into env.js');
			return false;
		} else if (backend_type === 'myport') {
			console.log( 'myport should insert_access_token before 401 occurs' );
			return false;
		} else {
			console.log('backend_type ' + backend_type + ' has no access to new authorization');
			return false;
		}
	}

	/**
	 * handle_response
	*/
	async handle_response()
	{
		var response = await this.call_the_api_request();

		/* 200
		**************************************************************************/
		if ( response.status === 200 ) {
			return response;
		}
		/* 401
		**************************************************************************/
		else if ( response.status === 401 ) {
			// 401 means request was unauthorized, get new token and repeat the request with old params
			var success = await this.unauthorized();
			if (!success) { return null; }
			return await this.prepare_params(this.params).handle_response();
		}
		/* 423
		**************************************************************************/
		else if ( response.status === 423 ) {
			// this error is thrown when agent code does not have valid clearance for current product group
			// agent code is set manually or from URL parameter &kod=(int)
			State.open_modal('clearance');
			return response;
		}
		/* 503
		**************************************************************************/
		else if ( response.status === 503 ) {
			// window.location.pathname = '/maintenance/';
		}
		/* 408
		**************************************************************************/
		else if ( response.status === 408 ) {
			console.log( '408 in request ' + this.params.action_name );
			return response;
		}
		/* action specific 422 423 425 409 500 504
		**************************************************************************/
		else {
			return response;
		}
	}

	/**
	 * call_the_api_request
	*/
	call_the_api_request()
	{
		return new Promise(
			( resolve, reject ) => {

				/* COUNTER REMOVED
				if ( this.params.counter > this.params.loop_max ) {
					return resolve( {status: 508, statusText: 'Loop Detected'} );
				}*/

				setTimeout(
					function() {
						return resolve( {status: 408, statusText: 'Request Timeout'} );
					},
					this.params.timeout * 1000
				);

				var storage = {};

				fetch( this.params.url, this.headers )
				.then(
					( response ) => {
						storage = response;
						return response.json();
					}
				).then(
					( response ) => {
						storage.result = response;
						resolve( storage );
					}
				);

			}
		);
	}
}

// without new keyword, each request has own iteration
export default Request_service;
