import { createStore } from 'vuex'

export default createStore({
	state: {
		access_token: '',
		session_token: '',
		endpoint_type: '',
		run_mode: '',
		backend_type: '',
		group_id: 0,
		step: 0,
		agent_code: '',
		skin: '',
		docs_template: '',
		modals: {
			preloader: false,
			loading: false,
			clearance: false,
			email: false,
		},
		user_input: {},
		proxy_endpoint: '',
		proxy_scope: '',
		workflow_status: {
			actions: [],
			finished: false,
			internal: false,
			step: -1,
			stepState: 'before_init',
		},
		form_storage: {},
		form_errors: {},
		selected_product: {
			product_id: '',
			form_id: '',
			next_contract: '',
			period: '',
		},
		doc_input_states: {},
		doc_input_requirements: {},
		doc_input_groups: {},
		doc_group_errors: {},
	},
	mutations: {
		access_token(state, payload) {
			state.access_token = payload.value;
		},
		session_token(state, payload) {
			state.session_token = payload.value;
		},
		endpoint_type(state, payload) {
			state.endpoint_type = payload.value;
		},
		backend_type(state, payload) {
			state.backend_type = payload.value;
		},
		run_mode(state, payload) {
			state.run_mode = payload.value;
		},
		group_id(state, payload) {
			state.group_id = payload.value;
		},
		step(state, payload) {
			state.step = payload.value;
		},
		agent_code(state, payload) {
			state.agent_code = payload.value;
		},
		skin(state, payload) {
			state.skin = payload.value;
		},
		docs_template(state, payload) {
			state.docs_template = payload.value;
		},
		open_modal(state, payload) {
			state.modals[payload.value] = true;
		},
		close_modal(state, payload) {
			state.modals[payload.value] = false;
		},
		user_input(state, payload) {
			state.user_input[payload.key] = payload.value;
		},
		delete_user_input(state) {
			state.user_input = {};
		},
		proxy_endpoint(state, payload) {
			state.proxy_endpoint = payload.value;
		},
		proxy_scope(state, payload) {
			state.proxy_scope = payload.value;
		},
		loading(state, payload) {
			state.modals.loading = payload.value;
		},
		workflow_status(state, payload) {
			state.workflow_status = payload.value;
		},
		form_storage(state, payload) {
			state.form_storage = payload.value;
		},
		form_errors(state, payload) {
			state.form_errors = payload.value;
		},
		selected_product(state, payload) {
			state.selected_product = payload.value;
		},
		doc_input_states(state, payload) {
			state.doc_input_states[payload.key] = payload.value;
		},
		doc_input_requirements(state, payload) {
			state.doc_input_requirements[payload.key] = payload.value;
		},
		doc_input_groups(state, payload) {
			state.doc_input_groups[payload.key] = payload.value;
		},
		doc_group_errors(state, payload) {
			state.doc_group_errors[payload.key] = payload.value;
		},
	},
	getters: {
	},
	actions: {
	},
	modules: {
	}
})
