		<!-- temporary helper -->
		<button @click="edit_user_data" class="btn">Nasp</button>

		<div id="formular" class="container agreement">

			<!-- navrh poistnej zmluvy a vsetky o poistnikovi ci poisteni -->
			<div v-html="html.getHtml" class="mb-4"></div>

			<!-- kym nebudu nacitane dokumenty zobraz loading -->
			<div v-show="!hide_loading" class="row">
				<div class="col pt-2 pb-5 text-center">
					<h3>Natavame dokumenty</h3>
					<div class="spinner">
						<div class="bounce1"></div>
						<div class="bounce2"></div>
						<div class="bounce3"></div>
					</div>
				</div>
			</div>

			<!-- multipurpose confirm button -->
			<div v-show="hide_loading" id="button_row" class="row">
				<div class="col px-4">
					<div class="row">
						<div class="col-8 text-left">
							<p id="confirm_validation" class="mt-2 mb-0 hidden">Pred pokraovanm muste potvrdi zvraznen zakrtvacie polika</p>
						</div>
						<div class="col-4 text-right">
							<button @click="run_confirm" id="confirm_button" type="button" class="btn btn-orange btn-right btn-lg">
								<span>{{ wording.confirm_row.span }}</span>
							</button>
						</div>
					</div>
				</div>
			</div>

			<!-- vzdy pritomny marketingovy suhlas + akceptovanie elektronickej komunikacie -->
			<div v-show="hide_loading" id="marketing_row" class="row">
				<div class="col px-4 pt-2">
					<div class="form-check py-2">
						<input name="suhlas_elekt_kom" class="form-check-input" id="suhlas_elekt_kom" v-model="checkboxes.suhlas_elekt_kom" type="checkbox">
						<label for="suhlas_elekt_kom" class="form-check-label"><a href="#" id="elekt_kom_modal_trigger">Shlas s elektronickou komunikciou</a></label>
					</div>
					<div class="form-check py-2">
						<input name="suhlas_marketing" class="form-check-input" id="suhlas_marketing" v-model="checkboxes.suhlas_marketing" type="checkbox">
						<label for="suhlas_marketing" class="form-check-label"><a href="#" id="marketing_modal_trigger">Shlas so spracovanm osobnch dajov pre ely marketingu</a></label>
					</div>
				</div>
			</div>

			<!-- multipurpose confirm row -->
			<div v-show="hide_loading" id="checkbox_row" class="row">
				<div class="col px-4 py-2">
					<p class="mb-0">{{ wording.confirm_row.paragraph }}</p>
					<div class="form-check py-2">
						<input name="suhlas_navrh" class="form-check-input" id="suhlas_navrh" v-model="checkboxes.suhlas_navrh" type="checkbox">
						<label for="suhlas_navrh" class="form-check-label">{{ wording.confirm_row.label }}</label>
					</div>
				</div>
			</div>

			<!-- dokumenty -->
			<div class="row">
				<div class="col">
					<div id="static_docs" class="mb-3 mt-2">
						<Document_download v-for="(value, key) in static_docs" :doc="value" />
					</div>
					<div id="session_docs">
						<Document_download v-for="(value, key) in session_docs" :doc="value" />
					</div>
				</div>
			</div>

			<!-- disclaimer -->
			<div class="row">
				<div class="col pt-3">
					<div v-html="html.disclaimer_html"></div>
				</div>
			</div>

		</div><!-- / Container -->
	</section>
</template>

<script>
	import State from '@/store/State.js';
	import Session_data from '@/logic/requests/Session_data.js';
	import Calculate_requests from '@/logic/requests/Calculate_requests.js';
	import Get_form_html from '@/logic/requests/Get_form_html.js';
	import Get_static_docs from '@/logic/requests/Get_static_docs.js';
	import Get_session_docs from '@/logic/requests/Get_session_docs.js';
	import Document_download from '@/components/form_app/documents/Document_download.vue';
	import Confirm_requests from '@/logic/requests/Confirm_requests.js';

	export default {
		props: {
		},
		components: {
			Document_download,
		},
		data() {
			return {
				special_data: {
					confirmed_by_provider: false,
					require_provider_confirm: false,
				},
				signature_required: 0,
				html: {
					getHtml: '',
					almost_done: '',
					disclaimer_html: '',
					marketing_moda_html: '',
					elekt_kom_modal_html: '',
				},
				static_docs: {},
				session_docs: {},
				loading_row: true,
				checkboxes: {
					suhlas_elekt_kom: false,
					suhlas_marketing: false,
					suhlas_navrh: false,
				},
				turbo: {
					repeat_count: 0,
					repeat_delay_fast: 2 * 1000, // 2 seconds
					repeat_delay_slow: 10 * 1000, // 10 seconds
					repeat_diminish: 10, // 0-20 seconds
					repeat_max: 26, // 20-180 seconds
				},
				wording: {
					confirm_row: {
						span: 'Dokoni',
						paragraph: '',
						label: 'Uveden nvrh na uzatvorenie poistnej zmluvy / nezvzn ponuku som si pretal(a) a shlasm nm/s ou. Tmto prehlasujem, e som bol oboznmen() s:',
					},
					sms_agent_row: {
						span: 'Odosla nvrh emailom',
						paragraph: 'Pre dokonenie zmluvy o poskytnut finannej sluby je potrebn potvrdenie klienta formou SMS autorizcie.',
						label: 'Totonos klienta som overil a shlasm s odoslanm dokumentcie na email klienta, aby dokonil autorizciu.',
					},
					client_row: {
						span: 'Vyiada SMS kd',
						paragraph: 'Pre dokonenie zmluvy o poskytnut finannej sluby je potrebn potvrdenie cez SMS kd - kliknutm na tlaitko "vyiada sms kd".',
						label: 'Oboznmil som sa s predzmluvnou dokumentciou, nemm iadne vhrady s jej obsahom a iadam o zaslanie SMS kdu na moje telefnne slo.',
					},
					provider_agent_row: {
						span: 'Odosla dokumentciu emailom',
						paragraph: 'Pre dokonenie zmluvy o poskytnut finannej sluby je potrebn potvrdenie klienta formou SMS autorizcie a potvrdenie na webe providera.',
						label: 'Totonos klienta som overil a shlasm s odoslanm dokumentcie na email klienta, aby dokonil autorizciu. Oboznmil som sa s predzmluvnou dokumentciou, nemm iadne vhrady s jej obsahom a iadam o presmerovanie na strnku providera.',
					},
				},
			}
		},
		created() {
			Session_data.session_info().then(
				(response) => {
					this.special_data = response.result?.specialData;
					this.signature_required = response.result?.signature_required;
				}
			).then(
				() => {
					return Session_data.get_data();
				}
			).then(
				(response) => {
					State.set_selected_product(
						response.result?.call_params?.product_id,
						response.result?.call_params?.form_id,
						response.result?.call_params?.next_contract,
						response.result?.call_params?.period,
					);
				}
			).then(
				() => {
					return Calculate_requests.check_calculate();
				}
			).then(
				(response) => {
					// console.log( response );
				}
			).then(
				() => {
					Get_static_docs.static_documents().then(
						(response) => {
							this.static_docs = response.result.documents;
						}
					);
					this.turbo_request();
					/* TEMPORARY DISABLE
					Get_form_html.get_it(false).then(
						(response) => {
							this.html.getHtml = response.result?.html;
						}
					);
					Get_form_html.get_it('view_header_text').then(
						(response) => {
							this.html.almost_done = response.result?.html;
						}
					);
					Get_form_html.get_it('view_agent_text').then(
						(response) => {
							this.html.disclaimer_html = response.result?.html;
						}
					);
					Get_form_html.get_it('view_agent_suhlas').then(
						(response) => {
							this.html.marketing_moda_html = response.result?.html;
						}
					);
					Get_form_html.get_it('view_agent_suhlas_ek').then(
						(response) => {
							this.html.elekt_kom_modal_html = response.result?.html;
						}
					);
					*/
				}
			);
		},
		methods: {
			run_confirm() {
				Confirm_requests.confirm_session(this.checkboxes).then(
					(response) => {
						if (response.status === 200) {
							State.change_step(5);
							return;
						}
						console.log( response.result );
					}
				);
			},
			turbo_request() {
				Get_session_docs.session_documents(false).then(
					(response) => {
						this.session_docs = response.result.data.documents;
						if (response.result.data.processingFinished === true) {
							this.loading_row = false;
							return;
						}
						if (this.turbo.repeat_count > this.turbo.repeat_max) {
							console.log( 'getsessiondocuments reached maximum repeat_count' );
							return;
						}
						// repeat request after delay
						this.turbo.repeat_count++;
						var delay = (this.turbo.repeat_count < this.turbo.repeat_diminish) ? this.turbo.repeat_delay_fast : this.turbo.repeat_delay_slow;
						setTimeout(
							function() {
								this.turbo_request();
							}.bind( this ),
							delay
						);
					}
				);
			},
			edit_user_data() {
				State.change_step(3);
			},
		},
		computed: {
			hide_loading() {
				return ! this.loading_row;
			},
		},
	}
</script>
