<template>
	<div :class="sii ? [] : ['multiple dl_item_file d-flex flex-column text-center', class_status]">
		<div :class="sii ? 'dl_title text-center text-sm-left' : 'dl_title text-center'">
			<h5>
				{{ heading_and_required }}
			</h5>
		</div>
		<div :class="sii ? ['dl_item_file d-flex flex-column flex-md-row align-items-stretch text-center text-md-left', class_status] : ['d-flex flex-column']">
			<div :class="sii ? 'dl_image d-flex pb-2 pt-3 p-md-2 mx-auto mx-md-0 mx-auto mx-md-0' : 'dl_image px-3 py-2'">
				<div class="img_wrapper">
					<img
						v-if="sii"
						src="@/assets/images/common/dl_img_doc_general.svg"
						width="67" height="67"
						class="my-auto"
					>
					<img
						v-else
						:src="image_sample"
						width="322" height="241.5"
					>
				</div>
			</div>
			<div
				v-if="sii"
				class="dl_filename pt-1 pb-0 py-md-4"
			>
				<p class="my-auto mx-auto mx-md-0">
					{{ filename }}
				</p>
			</div>
			<div :class="sii ? 'dl_up_download py-3 py-md-4 px-3' : 'dl_up_download py-3'">
				<input
					@change="picked_file"
					ref="file_input"
					type="file"
					id="{{ input.tags }}"
				>
				<div :class="sii ? 'btn-wrapper my-auto mx-auto mx-md-0' : 'btn-wrapper mx-auto'">
					<button
						@click="select_file"
						type="button"
						class="btn btn-primary btn-upload btn-m"
					>
						Vyberte sbor
					</button>
					<button
						@click="download_doc"
						:style="sii ? { visibility: allow_download ? 'visible' : 'hidden' } : { display: allow_download ? 'inline-block' : 'none' }"
						type="button"
						class="btn btn-primary btn-download btn-m btn-compact"
					>
						<!-- Stiahnu sbor -->
					</button>
				</div>
			</div>
			<div class="dl_progress">
				<div
					:style="{ height: number_solid + '%' }"
					class="dl_bar bar_vertical"
				></div>
				<div
					:style="{ height: number_opaque + '%', opacity: '0.3', }"
					class="dl_bar bar_vertical"
				></div>
				<div
					:style="{ width: number_solid + '%' }"
					class="dl_bar bar_horizontal"
				></div>
				<div
					:style="{ width: number_opaque + '%', opacity: '0.3', }"
					class="dl_bar bar_horizontal"
				></div>
			</div>
			<div :class="sii ? 'dl_status pt-3 pb-2 py-md-3 py-md-4 pl-md-3' : 'dl_status py-4 py-md-3 px-3'">
				<p :class="sii ? 'my-auto mx-auto mx-md-0' : 'my-auto mx-auto'">
					{{ user_info_status }}
				</p>
			</div>
			<div
				v-if="sii"
				class="dl_action pt-2 pb-3 py-md-3 py-md-4 px-3"
			>
				<button
					@click="send_to_provider"
					:disabled="send_disabled"
					type="button"
					class="btn btn-primary btn-send btn-m my-auto mx-auto mx-md-0"
				>
					Posla
				</button>
			</div>
		</div>
	</div>
</template>

<script>
	import Get_static_docs from '@/logic/requests/Get_static_docs.js';
	import Data_record from '@/logic/requests/Data_record.js';
	import Functions from '@/logic/Functions.js';
	import State from '@/store/State.js';

	export default {
		props: {
			input: {
				type: String, // single | multiple
				group: String, // only for multiple
				tags: String, // unique ID
				send: String, // DV token, only for single
				heading: String, // simple text
				image: String, // file name in our project folder
				state: Object, // stays null if dataRecord is empty, otherwise will populate
				required: Boolean, // only for multiple
			},
		},
		components: {
		},
		emits: ['refresh_session_docs'],
		data() {
			return {
				filename: '',
				error_message: '',
				preview: '',
				number_solid: 0,
				number_opaque: 0,
				number_increment: 0,
			}
		},
		created() {
			State.set_input_requirements(this.input.tags, this.input.required);
			State.set_input_groups(this.input.tags, this.input.group);
			if (this.input.state === null) {
				var status = 'empty';
			} else if (typeof this.input.state.dataRecords === 'object' && Object.keys(this.input.state.dataRecords).length > 0) {
				this.filename = this.input.state.name;
				var status = 'sent';
			} else {
				this.filename = this.input.state.name;
				var status = 'uploaded';
			}
			this.status(status);
			if (status === 'sent' || status === 'uploaded') {
				this.download_preview();
				this.number_solid = 100;
			}
		},
		methods: {
			select_file()
			{
				this.$refs.file_input.click();
			},
			status(status)
			{
				State.set_input_status(this.input.tags, status);
			},
			picked_file(event)
			{
				let max_mb = 3; // megabytes
				let bites = 1024 * 1024;
				this.status('uploading');
				this.progress_bar_loading();
				let file = event.target.files[0];
				if (file.size > max_mb * bites) {
					this.error_message = 'Presiahnut povolen vekos sboru ' + (Math.round((file.size / bites * 100)) / 100) + 'mb, max: ' + max_mb + 'mb';
					this.status('failed');
					return;
				}
				if (file.type !== 'application/pdf') {
					this.preview = URL.createObjectURL(file);
				}
				this.filename = Functions.slugify_but_keep_file_extension(file.name);
				let payload = new FormData();
				payload.append('file', file, this.filename);
				payload.append('title', this.filename);
				payload.append('description', 'uploaded by docs landing page');
				payload.append('type', file.type);
				payload.append('tags', this.input.tags);
				if (this.input.state !== null) {
					payload.append('id', this.input.state.id);
				}
				// for (let item of payload) {
				// 	console.log(item);
				// }
				this.upload_doc(payload);
			},
			upload_doc(payload)
			{
				Get_static_docs.upload_session_document(payload).then(
					(response) => {
						if (response.status === 200) {
							this.status('uploaded');
							this.$emit('refresh_session_docs');
							return;
						}
						this.status('failed');
						console.log(response);
					}
				);
			},
			download_doc()
			{
				let id = this.input.state.id;
				let name = this.input.state.name;
				let type = this.input.state.documentType;
				Get_static_docs.get_session_document(id).then(
					(response) => {
						Functions.downloadBase64(name, response.result.content, null, type);
					}
				);
			},
			download_preview()
			{
				if (this.input.state.documentType === 'application/pdf') {
					return;
				}
				let id = this.input.state.id;
				let name = this.input.state.name;
				Get_static_docs.get_session_document(id).then(
					(response) => {
						this.preview = 'data:image/jpeg;charset=utf-8;base64,' + response.result.content;
					}
				);
			},
			send_to_provider()
			{
				let record_token = this.input.send;
				if (record_token === undefined) {
					console.log('record_token is undefined, input has no send=... parameter');
					return;
				}
				this.status('sending');
				this.progress_bar_loading();
				Data_record.create_data_record(record_token).then(
					(response) => {
						if (response.status === 200) {
							this.status('sent');
							return;
						}
						this.error_message = response.result?.message;
						this.status('error');
					}
				);
			},
			progress_bar_loading()
			{
				this.number_solid = 0;
				this.number_opaque = 0;
				this.number_increment = 2;
				let faster = setInterval(
					() => {
						this.number_opaque = this.number_opaque + 2;
						if (this.number_opaque >= 100) {
							this.number_opaque = this.number_solid - 10;
						}
					},
					10
				);
				let interval = setInterval(
					() => {
						let status = State.get_input_status(this.input.tags);
						if (status === 'uploaded' || status === 'sent') {
							this.number_increment = 5;
						}
						if (this.number_increment > 0) {
							this.number_increment = this.number_increment - 0.03;
						} else {
							this.number_increment = 0;
						}
						this.number_solid = this.number_solid + this.number_increment;
						if (this.number_solid >= 100 || status === 'failed' || status === 'error') {
							this.number_solid = 100;
							clearInterval(interval);
							clearInterval(faster);
						}
					},
					50
				);
			},
		},
		computed: {
			send_disabled()
			{
				let status = State.get_input_status(this.input.tags);
				if (status === 'empty' || status === 'uploading' || status === 'sending') {
					return true;
				}
				return false;
			},
			user_info_status()
			{
				let status = State.get_input_status(this.input.tags);
				if (!this.sii && status === 'sending') {
					this.progress_bar_loading();
				}
				let info = {
					empty: 'Nahrajte prosm prlohu',
					uploading: 'Nahrvam...',
					uploaded: this.filename + ' nahrat',
					failed: this.error_message === '' ? 'nahrvanie zlyhalo' : this.error_message,
					sending: 'Odosielam...',
					sent: this.filename + ' odoslan do poisovne',
					error: this.sii ? this.error_message : State.get_group_errors(this.input.group),
				};
				return info[status];
			},
			class_status()
			{
				let status = State.get_input_status(this.input.tags);
				let classes = {
					empty: '',
					uploading: '',
					uploaded: 'dl_success',
					failed: 'dl_warning',
					sending: '',
					sent: 'dl_sent',
					error: 'dl_warning',
				};
				return classes[status];
			},
			allow_download()
			{
				let status = State.get_input_status(this.input.tags);
				if (status === 'empty' || status === 'uploading' || status === 'sending') {
					return false;
				}
				return true;
			},
			/*progress()
			{
				let status = State.get_input_status(this.input.tags);
				let percentage = {
					empty: 0,
					uploading: 25,
					uploaded: 50,
					failed: 10,
					sending: 80,
					sent: 100,
					error: 65,
				};
				return percentage[status];
			},*/
			sii()
			{
				// single input instance, not multiple
				if (this.input.type === 'single') {
					return true;
				}
				return false;
			},
			heading_and_required()
			{
				return this.input.heading + ((this.input.required) ? ' *' : '');
			},
			image_sample()
			{
				if (this.preview !== '') {
					return this.preview;
				}
				try {
					return require('@/assets/images/docs_landing_samples/' + this.input.image + '.jpg');
				} catch(error) {
					return require('@/assets/images/docs_landing_samples/placeholder_b.jpg');
				}
			},
		},
	}
</script>
