import State from '@/store/State.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Calculate_requests
*/
class Calculate_requests
{
	/**
	 * get_calculate
	*/
	get_calculate(id)
	{
		let requestParams = {
			action_name: 'calculate_step2',
			method: 'POST',
			url: State.endpoint_url() + 'online/calculate/' + '?' + Functions.field_format_parameter() + '&id=' + id + Functions.add_token_as_parameter(),
			body: null, // serialized product form data can be sent here
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {
				return response;
				/*// when calculate in step2 can not deliver product, we repeat results requests which should deliver when calculated
				if (response.status === 408) {
					// it delivers all calculated products, but class takes only one with its ID
					return repeat_results();
				} else {
					return response;
				}*/
			}
		).catch( (error) => { throw error; } );
	}


	/**
	 * check_calculate
	*/
	check_calculate()
	{
		let requestParams = {
			action_name: 'calculate_step4',
			method: 'POST',
			url: State.endpoint_url() + 'online/calculate/' + '?' + Functions.field_format_parameter() + Functions.add_token_as_parameter(),
			body: null,
		};
		requestParams.url += '&id=' + State.get_selected_product().product_id + '&period=' + State.get_selected_product().period;

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then().catch( (error) => { throw error; } );
	}
}

export default new Calculate_requests;
