<template>
	<section class="formulare">
		<div class="row">
			<div class="col-12">

				<!-- Title -->
				<Step_title :products_found="products_found" />

				<!-- Suhrn udajov -->
				<Summary :user_data="user_data" />

				<!-- TODO: globalForm -->
				<Global_form />

				<!-- TODO: filters - html and styles are temporary -->

				<!-- Vrchny loading, zmizne ked sa nacitaju vsetky produkty -->
				<Bouncer_loading v-show="loaders_active > 0" />

				<!-- Vypis vsetkych produktov -->
				<div>
					<Product_template v-for="(value, key) in products" :product_name="value" :product_id="key" @calculate_done="calculate_done" />
				</div>

				<!-- Spodny loading, zobrazi sa ked sa nacita prvy produkt, zmizne ked sa nacitaju vsetky produkty -->
				<Bouncer_loading v-show="loaders_active > 1" />

			</div>
		</div>
	</section>
</template>

<script>
	import Product_template from '@/components/form_app/product/Product_template.vue';
	import Product_requests from '@/logic/requests/Product_requests.js';
	import Step_title from '@/components/form_app/product/Step_title.vue';
	import Summary from '@/components/form_app/product/Summary.vue';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import Global_form from '@/components/form_app/product/Global_form.vue';

	export default {
		props: {
		},
		components: {
			Product_template,
			Step_title,
			Summary,
			Bouncer_loading,
			Global_form,
		},
		data() {
			return {
				loaded: 0,
				loaders_active: 1, // number of loaders displayed
				udaje: '',
				products: {},
			};
		},
		created() {
			Product_requests.get_product_list().then(
				(response) => {
					this.products = response.result.products;
				}
			);
		},
		methods: {
			calculate_done(udaje) {
				this.loaded++;
				this.udaje = udaje;
				if (this.loaded === Object.keys(this.products).length) {
					this.loaders_active = 0;
				} else {
					this.loaders_active = 2;
				}
			},
		},
		computed: {
			products_found() {
				if (this.loaded === 1) {
					return this.loaded + ' produkt';
				} else if (this.loaded > 1 && this.loaded < 5) {
					return this.loaded + ' produkty';
				} else /* 0 or 5+ */ {
					return this.loaded + ' produktov';
				}
			},
			user_data() {
				return this.udaje;
			},
		},
	}
</script>
