<template>
	<div :class="['row', (waiting) ? 'document_waiting' : '']">
		<div class="col-11">
			<a href="#" class="pr-2" @click="download_pdf">
				{{ name }}
			</a>
			<Bouncer_loading v-show="waiting" size="small" />
		</div>
		<div class="col-1">
			<a href="#" @click="download_pdf">
				<span class="i_download_item">
				</span>
			</a>
		</div>
	</div>
</template>

<script>
	import Get_static_docs from '@/logic/requests/Get_static_docs.js';
	import Functions from '@/logic/Functions.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';

	export default {
		props: {
			doc: Object,
		},
		components: {
			Bouncer_loading,
		},
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			download_pdf(event) {
				event.preventDefault();
				if (this.is_static) {
					Get_static_docs.get_static_document(this.doc.id).then(
						(response) => {
							Functions.downloadBase64(this.name, response.result.content);
						}
					);
				} else {
					if (this.waiting) {
						return;
					}
					Get_static_docs.get_session_document(this.doc.id).then(
						(response) => {
							Functions.downloadBase64(this.name, response.result.content);
						}
					);
				}
			},
		},
		computed: {
			is_static() {
				return (this.doc.filename === undefined) ? false : true;
			},
			name() {
				return (this.is_static) ? this.doc.filename : this.doc.name;
			},
			waiting() {
				if (this.is_static) {
					return false;
				}
				// waiting | available | error
				if (this.doc.documentState === 'waiting') {
					return true;
				} else if (this.doc.documentState === 'available') {
					return false;
				}
				// error?
				return true;
			},
		},
	}
</script>

<style scoped lang="scss">
	.document_waiting {
		pointer-events: none;
		cursor: wait;
		filter: grayscale(100%);
	}
</style>
