import State from '@/store/State.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Group_user_form
*/
class Group_user_form
{
	/************************************************************************************/
	/* public */
	/************************************************************************************/

	/**
	 * getform
	*/
	getform()
	{
		if (State.current_step() === 1) {
			return this.getgroupform();
		} else if (State.current_step() === 3) {
			return this.getuserform();
		}
	}


	/**
	 * validateform
	*/
	validateform()
	{
		if (State.current_step() === 1) {
			return this.validategroupform();
		} else if (State.current_step() === 3) {
			return this.validateuserform();
		}
	}


	/************************************************************************************/
	/* private */
	/************************************************************************************/

	/**
	 * getgroupform
	*/
	getgroupform(agent_code = undefined)
	{
		let requestParams = {
			action_name: 'getgroupform',
			method: 'GET',
			url: State.endpoint_url() + 'online/getgroupform/' + State.product_group_id() + '?',
		};
		requestParams.url += Functions.field_format_parameter();
		requestParams.url += Functions.add_token_as_parameter();
		requestParams.url += Functions.global_parameter();
		requestParams.url += (agent_code !== undefined) ? '&kod=' + agent_code : '';

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}

	/**
	 * validategroupform
	*/
	validategroupform()
	{
		let requestParams = {
			action_name: 'validategroupform',
			method: 'POST',
			body: Functions.serialize_user_input() + State.add_agent_code(),
			url: State.endpoint_url() + 'online/validategroupform/' + State.product_group_id() + '?',
		};
		requestParams.url += Functions.add_token_as_parameter();
		requestParams.url += Functions.global_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * getuserform
	*/
	getuserform()
	{
		let requestParams = {
			action_name: 'getform',
			method: 'POST',
			body: null,
			url: State.endpoint_url() + 'online/getform/' + State.get_selected_product().form_id + '?',
		};
		requestParams.url += Functions.field_format_parameter();
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * validateuserform
	*/
	validateuserform()
	{
		let requestParams = {
			action_name: 'validateform',
			method: 'POST',
			body: Functions.serialize_user_input(),
			url: State.endpoint_url() + 'online/validateform/' + State.get_selected_product().form_id + '?',
		};
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Group_user_form;
