import State from '@/store/State.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Product_requests
*/
class Product_requests
{
	/**
	 * get_product_list
	*/
	async get_product_list()
	{
		let requestParams = {
			action_name: 'getproducts',
			method: 'GET',
			url: State.endpoint_url() + 'online/getproducts/' + State.product_group_id() + '/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * select_product
	*/
	select_product()
	{
		let requestParams = {
			action_name: 'productselect',
			method: 'GET',
			url: State.endpoint_url() + 'online/productselect/' + State.get_selected_product().product_id + '/?' + Functions.add_token_as_parameter(),
		};
		requestParams.url += '&' + Functions.serialize_object(State.get_selected_product());

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Product_requests;
