<template>
	<section class="formulare">
		<div class="row">
			<div class="col-md-12">

				<Tabs :tab_current="tab_current" />

				<Prev_next_buttons @prev="prev()" @next="next()" :show_back="show_back" />

			</div>
		</div>
	</section>
</template>

<script>
	import Prev_next_buttons from '@/components/form_app/nav/Prev_next_buttons.vue';
	import Group_user_form from '@/logic/requests/Group_user_form.js';
	import Functions from '@/logic/Functions.js';
	import Tabs from '@/components/forms/Tabs.vue';
	import State from '@/store/State.js';

	export default {
		props: {
		},
		components: {
			Prev_next_buttons,
			Tabs,
		},
		data() {
			return {
				tab_current: 0,
				tab_last: 0,
				is_step: 1,
			}
		},
		created() {
			State.loading('ON');
			this.is_step = State.current_step();
			Group_user_form.getform().then(
				(response) => {
					State.loading('OFF');
					if (response.status !== 200) {
						return;
					}
					State.store_session_params_do_not_overwrite(response.result);
					var form = response.result.form;
					State.store_the_form(form);
					this.tab_last = Object.keys(form)[ Object.keys(form).length - 1 ];
					this.tab_current = parseInt( Object.keys(form)[0], 10 );
					// this.tab_first = this.tab_current; ??? mozno bude treba ak je prvy tab neexistuje
				}
			);
		},
		methods: {
			prev() {
				if (this.tab_current > 1) {
					this.tab_current--;
					var active_tab = Functions.active_tab(this.tab_current);
					if (active_tab === undefined /*missing tab*/ || Object.keys(active_tab).length === 0 /*all hidden*/) {
						this.prev();
					}
				} else if (this.is_step === 3) {
					State.clear_the_form();
					State.change_step(2);
				}
			},
			next() {
				if (this.tab_current < this.tab_last) {
					Group_user_form.validateform();
					this.tab_current++;
					var active_tab = Functions.active_tab(this.tab_current);
					if (active_tab === undefined /*missing tab*/ || Object.keys(active_tab).length === 0 /*all hidden*/) {
						this.next();
					}
					return;
				}
				State.loading('ON');
				Group_user_form.validateform().then(
					(response) => {
						State.loading('OFF');
						if (response.result !== undefined && response.result.validform) {
							State.clear_the_form();
							State.change_step(this.is_step + 1);
						} else {
							var validation = response.result.form_validation_error_elements;
							if (validation !== undefined && typeof validation === 'object') {
								State.set_form_errors(validation);
								this.show_first_tab_with_error();
							}
						}
					}
				);
			},
			show_first_tab_with_error() {
				var form = State.retrieve_the_form();
				for (var tab_number in form) {
					if ( ! form.hasOwnProperty( tab_number ) ) { continue; }
					for (var input_order in form[tab_number]) {
						if ( ! form[tab_number].hasOwnProperty( input_order ) ) { continue; }
						for (var input_id in form[tab_number][input_order]) {
							if ( ! form[tab_number][input_order].hasOwnProperty( input_id ) ) { continue; }
							var errors = State.get_form_errors();
							if (errors.hasOwnProperty(input_id)) {
								this.tab_current = parseInt(tab_number, 10);
								return;
							}
						}
					}
				}
			},
		},
		computed: {
			show_back() {
				return (this.tab_current > 1 || this.is_step === 3) ? true : false;
			},
		},
	}
</script>
