<template>
	<div :class="['osm_app_scope']" data-cy="osm_app_dom_root">
		<div :class="['container', loading_class, skin]">
			<div class="page_wrap">
				<Flow_sandbox v-if="mode === 'node_red'" />
				<View_switcher v-if="mode === 'form_app'" />
				<Docs_template v-if="mode === 'docs_landing'" />
			</div>
			<Modal_switcher />
		</div>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import View_switcher from '@/components/form_app/View_switcher.vue';
	import Modal_switcher from '@/components/modals/Modal_switcher.vue';
	import Flow_sandbox from '@/components/flow/Flow_sandbox.vue';
	import Docs_template from '@/components/docs_landing/Docs_template.vue';
	import { version } from '@/../package.json';

	export default {
		components: {
			View_switcher,
			Modal_switcher,
			Flow_sandbox,
			Docs_template,
		},
		inject: ['config'],
		data() {
			return {
			}
		},
		methods: {
		},
		created() {
			State.save_the_config(this.config);
			console.log('osm_app:', version);
		},
		computed: {
			skin() {
				return State.skin_class();
			},
			loading_class() {
				if (State.give_modal_state('loading')) {
					return 'loading';
				} else {
					return '';
				}
			},
			mode() {
				return State.run_mode();
			},
		},
	}
</script>

<style scoped lang="scss">
	.osm_app_scope::v-deep() {
		@import '@/style_imports.scss';
	}
	.loading > .page_wrap {
		opacity: 0.6;
		pointer-events: none;
	}
</style>
