<template>
	<div class="row">
		<div class="col-md-12 text-center">
			<h2>
				Nali sme <span class="text-orange">{{ products_found }}</span>
			</h2>
			<div class="results-send-export">
				<button class="link" @click="open_email_modal">
					<span class="i_email"></span>posla e-mailom
				</button>
				<button class="link" @click="download_pdf">
					<Bouncer_loading v-if="downloading_pdf" size="small" />
					<span v-else class="i_download"></span>
					stiahnu pdf
				</button>
			</div>
		</div>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Get_pdf from '@/logic/requests/Get_pdf.js';
	import Functions from '@/logic/Functions.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';

	export default {
		props: {
			products_found: String,
		},
		components: {
			Bouncer_loading,
		},
		data() {
			return {
				downloading_pdf: false,
			}
		},
		created() {
		},
		methods: {
			open_email_modal() {
				State.open_modal('email');
			},
			download_pdf() {
				this.downloading_pdf = true;
				Get_pdf.download_global_pdf().then(
					(response) => {
						this.downloading_pdf = false;
						Functions.downloadBase64('Ponuka.pdf', response.result.pdf);
					}
				);
			},
		},
		computed: {
		},
	}
</script>
