<template>
	<div class="docs_landing_scope">
		<div v-for="(value, key) in clear_template_data" >
			<div
				v-if="Object.keys(value)[0] === 'html_key'"
				v-html="value.html_key"
				class="dl_item_text"
			></div>
			<Send_button
				v-else-if="Object.keys(value)[0] === 'button_key'"
				:input="value.button_key"
				@refresh_session_docs="refresh_session_docs"
			/>
			<Upload_input
				v-else-if="Object.keys(value)[0] === 'input_key'"
				:input="value.input_key[0]"
				@refresh_session_docs="refresh_session_docs"
			/>
			<div
				v-else-if="Object.keys(value)[0] === 'multiple_key'"
				class="row"
			>
				<div
					v-for="(value2, key2) in value.multiple_key"
					class="col-12 col-md-4"
				>
					<Upload_input
						:input="value2"
						@refresh_session_docs="refresh_session_docs"
					/>
				</div>
			</div>
		</div>
		<div v-if="debug" >
			<hr>
			<pre>
				{{ json_result }}
			</pre>
		</div>
		<Testing_template />
	</div>
</template>

<script>
	import Upload_input from '@/components/docs_landing/Upload_input.vue';
	import Get_form_html from '@/logic/requests/Get_form_html.js';
	import Get_session_docs from '@/logic/requests/Get_session_docs.js';
	import State from '@/store/State.js';
	import Testing_template from '@/components/docs_landing/Testing_template.vue';
	import Send_button from '@/components/docs_landing/Send_button.vue';
	import Session_data from '@/logic/requests/Session_data.js';

	export default {
		props: {
		},
		components: {
			Upload_input,
			Testing_template,
			Send_button,
		},
		data() {
			return {
				debug: false,
				template: '',
				json_result: '',
				session_docs_state: {},
				clear_data: {},
				debug_template: `
					<h2>Document upload landing page</h2>
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					%% type=single tags=kapota send=dv_send_kapota heading="Kapota auta" %%
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					%% type=single tags=technicak send=dv_send_technicak heading="Technick preukaz" %%
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					%%
						type=multiple group=kufre
						%
						tags=kufor1 heading="Kufor 1" image="07_lavobok" required=1
						%
						tags=kufor2 heading="Kufor 2" image="08_pravobok" required=1
						%
						tags=kufor3 heading="Kufor 3" image="09_celne_sklo" required=1
						%
						tags=kufor4 heading="Kufor 4" required=0
						%
						tags=kufor5 heading="Kufor 5" required=0
					%%
					%% send_button send=dv_send_for_all group=kufre text="Deus vult dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam" %%
				`,
			}
		},
		created() {
			if (this.we_dont_have_token()) {
				return;
			}
			Promise.resolve().then(
				() => {
					return this.get_template_token_name();
				}
			).then(
				() => {
					return Promise.all(
						[
							this.get_document_template(),
							this.get_session_documents(),
						]
					);
				}
			).then(
				() => {
					this.parse_template_object();
				}
			);
		},
		methods: {
			we_dont_have_token()
			{
				if (State.get_session_token() === '') {
					console.log('Session token is required.');
					return true;
				}
				return false;
			},
			get_template_token_name()
			{
				return Session_data.get_data().then(
					(response) => {
						let name = response.result?.call_params?.osm_doc_template || 'upload_placeholder';
						console.log( 'using template: ' + name );
						State.save_document_page_template(name);
					}
				);
			},
			get_document_template()
			{
				if (this.debug) {
					return new Promise(
						(resolve, reject) => {
							this.template = this.debug_template;
							resolve();
						}
					);
				} else {
					let docs_template = State.document_page_template();
					return Get_form_html.get_it(docs_template).then(
						(response) => {
							if (response.result.error !== undefined) {
								this.template = response.result.error;
								return;
							}
							this.template = response.result.html;
						}
					);
				}
			},
			get_session_documents()
			{
				return Get_session_docs.session_documents(true).then(
					(response) => {
						this.json_result = JSON.stringify(response, null, 2);
						this.session_docs_state = response;
					}
				);
			},
			refresh_session_docs()
			{
				return; // this emit seems useless anyway
				Promise.all(
					[
						this.get_session_documents(),
					]
				).then(
					() => {
						this.parse_template_object();
					}
				);
			},
			add_document_state_by_tags(tags)
			{
				let docs = this.session_docs_state?.result?.data?.documents;
				if (docs === undefined) {
					return null; // can this even happen?
				}
				var i, len = docs.length;
				for (i = 0; i < len; i++) {
					if (tags === docs[i].tags?.[0]) {
						return docs[i];
					}
				}
				return null;
			},
			parse_template_object()
			{
				let split = this.template.split('%%');
				let clear = [];
				let i, len = split.length;
				for (i = 0; i < len; i++) {
					let occurence = split[i].replace(/\n/g, '').replace(/\t/g, '').trim();
					// send button for multiple inputs
					if (occurence.indexOf('send_button') !== -1) {
						clear.push(
							{
								button_key: {
									send: occurence.match(/send=\w+/)?.[0].replace('send=', ''),
									text: occurence.match(/text="(.*?)"/)?.[0].replace('text="', '').replace( '"', '' ),
									group: occurence.match(/group=\w+/)?.[0].replace('group=', ''),
								}
							}
						);
					}
					// html doesnt have ..type=..
					else if (occurence.match(/type=\w+/) === null) {
						if (occurence === '') {
							continue;
						}
						clear.push(
							{
								html_key: occurence
							}
						);
					}
					// otherwise its input data
					else {
						clear.push(
							this.create_input_object(occurence)
						);
					}
				}
				console.log(clear);
				this.clear_data = clear;
			},
			create_input_object(occurence)
			{
				let type = occurence.match(/type=\w+/)?.[0].replace('type=', '');
				// single
				if (type === 'single') {
					let many = [];
					let tags = occurence.match(/tags=\w+/)?.[0].replace('tags=', '');
					let send = occurence.match(/send=\w+/)?.[0].replace('send=', '');
					let heading = occurence.match(/heading="(.*?)"/)?.[0].replace('heading="', '').replace( '"', '' );
					let state = this.add_document_state_by_tags(tags);
					many.push(
						{
							type: type,
							group: null,
							tags: tags,
							send: send,
							heading: heading,
							image: null,
							state: state,
							required: false,
						},
					);
					return { input_key: many };
				// multiple
				} else if (type === 'multiple') {
					let many = [];
					let group = occurence.match(/group=\w+/)?.[0].replace('group=', '');
					let parts = occurence.split('%');
					let i, len = parts.length;
					for (i = 0; i < len; i++) {
						if (parts[i].match(/type=multiple/)) {
							continue;
						}
						let tags = parts[i].match(/tags=\w+/)?.[0].replace('tags=', '');
						let heading = parts[i].match(/heading="(.*?)"/)?.[0].replace('heading="', '').replace( '"', '' );
						let image = parts[i].match(/image="(.*?)"/)?.[0].replace('image="', '').replace( '"', '' );
						let required = parts[i].match(/required=1/) ? true : false;
						let state = this.add_document_state_by_tags(tags);
						many.push(
							{
								type: type,
								group: group,
								tags: tags,
								send: null,
								heading: heading,
								image: image,
								state: state,
								required: required,
							}
						);
					}
					return { multiple_key: many };
				} else {
					console.log('Type parameter was undefined.');
				}
			},
		},
		computed: {
			clear_template_data()
			{
				return this.clear_data;
			},
		},
	}
</script>
