<template>
	<Modal_body name="clearance" :close="true" title="Chbajca licencia" >
		<!-- <div class="col-12 col-sm-3 text-center"> -->
			<!-- <img alt="maskot-stop" class="mt-3" src="<?php echo get_template_directory_uri() . '/images/maskot-stop.png' ?>"> -->
		<!-- </div> -->
		<!-- <div class="col-12 col-sm-9 text-center text-sm-left"> -->
		<div class="col-12 text-center text-sm-left">
			<p>Pre prcu s tmto produktom nemte poadovan licenciu.</p>

			<form v-show="!loading" @submit="submit_clearance">
				<input v-model="value" name="agent_clearance" class="form-control mb-2" type="text" placeholder="Doplte kd agenta">
				<button class="btn btn-primary mb-3" type="submit">
					Odosla
				</button>
			</form>

			<div v-show="loading" class="mb-3">
				<h4>Overujeme kd</h4>
				<Bouncer_loading />
			</div>

			<div v-show="failed" class="mb-3">
				<h4>Overovanie kdu zlyhalo.</h4>
			</div>

			<p>
				Ak dolo k nedorozumeniu, prosm kontaktuje ns <br>na e-mailovej adrese:
				<a :href="mailto">{{ email }}</a>
			</p>
		</div>
	</Modal_body>
</template>

<script>
	import Constants from '@/logic/Constants.js';
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Group_user_form from '@/logic/requests/Group_user_form.js';
	import State from '@/store/State.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';

	export default {
		props: {
		},
		components: {
			Modal_body,
			Bouncer_loading,
		},
		data() {
			return {
				loading: false,
				failed: false,
				value: '',
			}
		},
		created() {
		},
		methods: {
			submit_clearance(event) {
				event.preventDefault();
				this.loading = true;
				Group_user_form.getgroupform(this.value).then(
					(response) => {
						this.loading = false;
						if (response.status !== 200) {
							this.failed = true;
							return;
						}
						State.close_modal('clearance');
						// State.store_session_params_do_not_overwrite(response.result);
						State.store_the_form(response.result.form);
					}
				);
			}
		},
		computed: {
			email() {
				return Constants.email_address();
			},
			mailto() {
				return 'mailto:' + this.email;
			},
		},
	}
</script>
