import store from '@/store/index.js';

class State
{
	get(name)
	{
		return store.state[name];
	}

	set(name, value, key = null)
	{
		store.commit(
			{
				type: name,
				value: value,
				key: key,
			}
		);
	}

	save_the_config(config)
	{
		this.set('step', config.step);
		this.set('backend_type', config.backend_type);
		this.set('run_mode', config.run_mode);
		this.set('access_token', config.access_token);
		this.set('session_token', config.session_token);
		this.set('endpoint_type', config.endpoint_type);
		this.set('group_id', config.group_id);
		this.set('agent_code', config.agent_code);
		this.set('skin', config.skin);
		this.set('proxy_endpoint', config.proxy_endpoint);
		this.set('proxy_scope', config.proxy_scope);
	}

	loading(state)
	{
		if (state === 'ON') {
			this.set('loading', true);
		} else if (state === 'OFF') {
			this.set('loading', false);
		} else {
			throw 'loading baaad';
		}
	}

	open_modal(name)
	{
		this.set('open_modal', name);
	}

	close_modal(name)
	{
		this.set('close_modal', name);
	}

	give_modal_state(name)
	{
		return this.get('modals')[name];
	}

	current_step()
	{
		return this.get('step');
	}

	change_step(int)
	{
		this.set('step', int);
	}

	product_group_id()
	{
		return this.get('group_id');
	}

	endpoint_type()
	{
		return this.get('endpoint_type');
	}

	endpoint_url()
	{
		var endpoints = {
			stage: 'https://api-stage.finportal.sk/esb/online-sale-mw/',
			edu: 'https://api-edu.finportal.sk/esb/online-sale-mw/',
			prod: 'https://backoffice.finportal.sk/esb/online-sale-mw/',
			develop: this.get('proxy_endpoint') + '/online-sale-mw/',
			dev: 'https://api-develop.intechon.sk/esb/online-sale-mw/',
		};
		return endpoints[ this.endpoint_type() ];
	}

	give_api_scope()
	{
		return this.get('proxy_scope');
	}

	get_session_token()
	{
		return this.get('session_token');
	}

	set_session_token(token)
	{
		this.set('session_token', token);
	}

	store_session_params_do_not_overwrite(result)
	{
		if (this.get('session_token') === '') {
			this.set('session_token', result.token);
		}
		if (this.get('agent_code') === '' && result.call_params !== undefined && result.call_params.kod !== undefined) {
			this.set('agent_code', result.call_params.kod);
		}
	}

	save_bearer(auth)
	{
		this.set('access_token', auth);
		if (auth === '503' || auth === 503) {
			this.change_step(0);
		}
	}

	give_bearer()
	{
		return 'Bearer ' + this.get('access_token');
	}

	save_user_input(key, value)
	{
		this.set('user_input', value, key);
	}

	retrieve_user_input(key)
	{
		return this.get('user_input')[key];
	}

	get_all_user_inputs()
	{
		return this.get('user_input');
	}

	clear_all_user_inputs()
	{
		this.set('delete_user_input');
	}

	add_agent_code()
	{
		var code = this.get('agent_code');
		if (code === '') {
			return '';
		}
		return '&kod=' + code;
	}

	get_backend_type()
	{
		return this.get('backend_type');
	}

	skin_class()
	{
		return 'skin-' + this.get('skin');
	}

	save_document_page_template(name)
	{
		this.set('docs_template', name);
	}

	document_page_template()
	{
		return this.get('docs_template');
	}

	run_mode()
	{
		return this.get('run_mode');
	}

	set_workflow_status(status)
	{
		if (
			typeof status.actions !== 'object' ||
			typeof status.finished !== 'boolean' ||
			typeof status.internal !== 'boolean' ||
			typeof status.step !== 'number' ||
			typeof status.stepState !== 'string' ||
			! ['pending', 'processing', 'success', 'error'].includes( status.stepState )
		) {
			console.dir( 'status: ' + typeof status, status );
			throw new TypeError('workflow_status invalid');
		}
		this.set('workflow_status', status);
	}

	get_workflow_actions()
	{
		return this.get('workflow_status').actions;
	}

	get_workflow_finished()
	{
		return this.get('workflow_status').finished;
	}

	get_workflow_internal()
	{
		return this.get('workflow_status').internal;
	}

	get_workflow_step()
	{
		return this.get('workflow_status').step;
	}

	get_workflow_step_state()
	{
		return this.get('workflow_status').stepState;
	}

	clear_workflow_status()
	{
		this.set(
			'workflow_status',
			{
				actions: [],
				finished: false,
				internal: false,
				step: -1,
				stepState: 'before_init',
			}
		);
	}

	set_form_errors(error_object)
	{
		this.set('form_errors', error_object);
	}

	get_form_errors()
	{
		return this.get('form_errors');
	}

	store_the_form(form)
	{
		this.set('form_storage', form);
	}

	retrieve_the_form()
	{
		return this.get('form_storage');
	}

	clear_the_form()
	{
		this.set('form_storage', {});
	}

	set_selected_product(product_id, form_id, next_contract, period)
	{
		this.set(
			'selected_product',
			{
				product_id: product_id,
				form_id: form_id,
				next_contract: next_contract,
				period: period,
			}
		);
	}

	get_selected_product()
	{
		return this.get('selected_product');
	}

	set_input_status(tags, status)
	{
		let allowed = ['empty', 'uploading', 'uploaded', 'failed', 'sending', 'sent', 'error'];
		if (allowed.indexOf(status) === -1) {
			console.log('baaad status');
			return;
		}
		this.set('doc_input_states', status, tags);
	}

	get_input_status(tags)
	{
		return this.get('doc_input_states')[tags];
	}

	set_input_requirements(tags, required)
	{
		this.set('doc_input_requirements', required, tags);
	}

	get_input_requirements()
	{
		return this.get('doc_input_requirements');
	}

	set_input_groups(tags, group)
	{
		this.set('doc_input_groups', group, tags);
	}

	get_input_groups()
	{
		return this.get('doc_input_groups');
	}

	set_group_errors(group, error)
	{
		this.set('doc_group_errors', error, group);
	}

	get_group_errors(group)
	{
		return this.get('doc_group_errors')[group];
	}
}

export default new State;
