<template>
	<div class="dl_item_action">
		<div class="row">
			<div class="col-12 col-sm-9 col-md-10 my-auto">
				<p>
					{{ input.text }}
				</p>
			</div>
			<div class="col-12 col-sm-3 col-md-2 text-center text-sm-right">
				<button
					@click="send_to_provider"
					:disabled="send_disabled"
					type="button"
					class="btn btn-primary btn-send btn-m mt-2 mt-md-0"
					:title="input.group"
				>
					Posla
				</button>
			</div>
		</div>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Data_record from '@/logic/requests/Data_record.js';

	export default {
		props: {
			input: {
				send: String,
				text: String,
				group: String,
			},
		},
		components: {
		},
		emits: ['refresh_session_docs'],
		data() {
			return {
				grouped_tags: [],
				required_tags: [],
			}
		},
		created() {
			let groups = State.get_input_groups();
			for (var key in groups) {
				if (!groups.hasOwnProperty(key)) { continue; }
				if (groups[key] === this.input.group) {
					this.grouped_tags.push(key);
				}
			}
			let required = State.get_input_requirements();
			for (var key in required) {
				if (!required.hasOwnProperty(key)) { continue; }
				if (required[key] === true && this.grouped_tags.indexOf(key) !== -1) {
					this.required_tags.push(key);
				}
			}
		},
		methods: {
			status(status)
			{
				var i, len = this.grouped_tags.length;
				for ( i = 0; i < len; i++ ) {
					if (State.get_input_status(this.grouped_tags[i]) === 'empty') {
						continue;
					}
					State.set_input_status(this.grouped_tags[i], status);
				}
			},
			send_to_provider()
			{
				let record_token = this.input.send;
				if (record_token === undefined) {
					console.log('record_token is undefined, input has no send=... parameter');
					return;
				}
				this.status('sending');
				Data_record.create_data_record(record_token).then(
					(response) => {
						if (response.status === 200) {
							this.status('sent');
							this.$emit('refresh_session_docs'); // added
							return;
						}
						State.set_group_errors(this.input.group, response.result?.message);
						this.status('error');
					}
				);
			},
		},
		computed: {
			send_disabled()
			{
				let disabled = false;
				var i, len = this.required_tags.length;
				for ( i = 0; i < len; i++ ) {
					let status = State.get_input_status(this.required_tags[i]);
					if (status === 'empty' || status === 'uploading' || status === 'sending') {
						disabled = true;
					}
				}
				return disabled;
			},
		},
	}
</script>
