import State from '@/store/State.js';
import Show_hide from '@/logic/Show_hide.js';

/**
 * Functions
*/
class Functions
{
	/**
	 * serialize_user_input
	*/
	serialize_user_input()
	{
		var inputs = State.get_all_user_inputs();
		var serialized = [];

		for ( var key in inputs ) {
			if ( ! inputs.hasOwnProperty( key ) ) { continue; }

			var value = encodeURIComponent( key ) + '=' + encodeURIComponent( inputs[key] );
			serialized.push( value );

		}

		return serialized.join( '&' );
	}


	/**
	 * serialize_object
	*/
	serialize_object(object)
	{
		var serialized = [];

		for ( var key in object ) {
			if ( ! object.hasOwnProperty( key ) ) { continue; }

			var value = encodeURIComponent( key ) + '=' + encodeURIComponent( object[key] );
			serialized.push( value );

		}

		return serialized.join( '&' );
	}


	/**
	 * custom_serialize_form_elements
	*/
	custom_serialize_form_elements(submit_event)
	{
		// setup our serialized data
		var serialized = [];

		// loop through each field in the form
		var i, len = submit_event.target.elements.length;
		for ( i = 0; i < len; i++ ) {

			var field = submit_event.target.elements[i];

			// don't serialize fields without a name, submits, buttons, file, reset inputs, disabled fields and fieldsets
			if (
				! field.name ||
				/*field.disabled || in NP we are sending disabled fields as well*/
				field.type === 'file' ||
				field.type === 'reset' ||
				field.type === 'submit' ||
				field.type === 'button' ||
				field.type === 'fieldset'
			) {
				continue;
			}

			// if a multi-select, get all selections
			if ( field.type === 'select-multiple' ) {
				for ( var n = 0; n < field.options.length; n++ ) {
					if ( !field.options[n].selected ) {
						continue;
					}
					serialized.push(
						encodeURIComponent( field.name ) + '=' + encodeURIComponent( field.options[n].value )
					);
				}
			}

			// convert field data to a query string, its for type text / textarea / checked radios / select (maybe more)
			else if (
				( field.checked === true && field.type === 'radio' )
				||
				( field.type !== 'radio' && field.type !== 'checkbox' )
			) {
				serialized.push(
					encodeURIComponent( field.name ) + '=' + encodeURIComponent( field.value )
				);
			}

			// unchecked checkboxes usually doesnt send any data, this fixes issue by forced sending "0"
			else if (
				field.type === 'checkbox'
				&&
				field.checked === false
			) {
				serialized.push(
					encodeURIComponent( field.name ) + '=0'
				);
			}

			// overwriting default checkbox value "on" to value "1"
			else if (
				field.type === 'checkbox'
				&&
				field.checked === true
			) {
				serialized.push(
					encodeURIComponent( field.name ) + '=1'
				);
			}

			// fallback
			else {
				if ( field.type !== 'radio' ) {
					console.log( 'field value not collected ' + field.name );
				}
			}

		}

		return serialized.join( '&' );
	}


	/**
	 * global_parameter
	*/
	global_parameter()
	{
		if ( State.endpoint_type() !== 'stage' ) {
			return '&global=0'; // old way, sending step1 and globalForm in one request
		}
		if ( State.current_step() === 1 ) {
			return '&global=2'; // ask for | validate only step1 form
		} else if ( State.current_step() === 2 ) {
			return '&global=1'; // ask for | validate only globalForm separately
		} else {
			return '';
		}
	}

	/**
	 * field_format_parameter
	*/
	field_format_parameter()
	{
		return 'json=1';
	}

	/**
	 * add_token_as_parameter (used to be in Session)
	*/
	add_token_as_parameter()
	{
		var token = State.get_session_token();
		if ( token === '' ) {
			return '';
		}
		return '&token=' + token;
	}

	/**
	 * value_from_storage_or_session
	*/
	value_from_storage_or_session(field_id, field_data, default_value)
	{
		var storage = State.retrieve_user_input(field_id);
		if (storage !== undefined) {
			return storage;
		} else if (field_data.value !== null) {
			State.save_user_input(field_id, field_data.value);
			return field_data.value;
		} else {
			return default_value;
		}
	}


	/**
	 * base 64 image functions
	 * works well, somehow
	*/
	downloadBase64(name, base64Data, arrBuffer = null, documentType = 'application/pdf') {
		if (arrBuffer == null)
			arrBuffer = this.base64ToArrayBuffer(base64Data);

		// It is necessary to create a new blob object with mime-type explicitly set
		// otherwise only Chrome works like it should
		var newBlob = new Blob([arrBuffer], { type: documentType });

		// IE doesn't allow using a blob object directly as link href
		// instead it is necessary to use msSaveOrOpenBlob
		if (window.navigator && window.navigator.msSaveOrOpenBlob) {
			window.navigator.msSaveOrOpenBlob(newBlob, name);
			return;
		}

		// For other browsers:
		// Create a link pointing to the ObjectURL containing the blob.
		var data = window.URL.createObjectURL(newBlob);

		var link = document.createElement('a');
		document.body.appendChild(link); //required in FF, optional for Chrome
		link.href = data;
		link.download = name;
		link.click();
		window.URL.revokeObjectURL(data);
		link.remove();
	}
	base64ToArrayBuffer(data) {
		var binaryString = window.atob(data);
		var binaryLen = binaryString.length;
		var bytes = new Uint8Array(binaryLen);
		for (var i = 0; i < binaryLen; i++) {
			var ascii = binaryString.charCodeAt(i);
			bytes[i] = ascii;
		}
		return bytes;
	}


	/**
	 * active_tab
	*/
	active_tab(tab_current)
	{
		var inputs = State.retrieve_the_form()[tab_current];
		var clear = {};
		for ( var order in inputs ) {
			if ( ! inputs.hasOwnProperty( order ) ) { continue; }
			// Show_hide for input with its wrap
			var show_hide_instance = new Show_hide(inputs[order]);
			if (show_hide_instance.visibility_boolean(inputs[order])) {
				clear[order] = inputs[order];
			}
		}
		return clear;
	}


	/**
	 * group_offer
	*/
	group_offer()
	{
		let offers = {
			1: 'offer_pzp',
			2: 'offer_kasko',
			3: 'offer_hypoteka',
			4: 'offer_zivot',
			5: 'offer_gap',
			6: 'offer_sporenie',
			7: 'offer_cestovne',
			8: 'offer_zodpovednost',
			9: 'offer_majetok',
			10: 'offer_podnikatelia',
			72: 'offer_sedadla',
			82: 'offer_test',
			73: 'offer_cudzinci',
			85: 'offer_dss',
			86: 'offer_dds',
			88: 'offerzdravotka',
		};
		let id = State.product_group_id();
		if (!offers.hasOwnProperty(id)) {
			console.log('ERROR: undefined product group offer');
			return '';
		}
		return offers[id];
	}


	/**
	 * Slugify - creat slug from any string
	*/
	slugify(string)
	{
		const a = '/_,:;';
		const b = 'aaaaaaaacceeeeghiiiilmnnnnoooooprssssttuuuuuwxyyzz------';
		const p = new RegExp(a.split('').join('|'), 'g');

		return string.toString().toLowerCase()
			.replace(/\s+/g, '-') // Replace spaces with -
			.replace( p, function(c) { return b.charAt(a.indexOf(c)) } ) // Replace special characters
			.replace(/&/g, '-and-') // Replace & with 'and'
			.replace(/[^\w\-]+/g, '') // Remove all non-word characters
			.replace(/\-\-+/g, '-') // Replace multiple - with single -
			.replace(/^-+/, '') // Trim - from start of text
			.replace(/-+$/, '') // Trim - from end of text
	}


	/**
	 * slugify_but_keep_file_extension
	*/
	slugify_but_keep_file_extension(string)
	{
		let dot_position = string.lastIndexOf('.');
		let filename = string.substring(0, dot_position);
		let extension = string.substring(dot_position);
		let slug = this.slugify(filename);
		return slug + extension;
	}
}

export default new Functions;
